 padding-right: 20px;\n        cursor: pointer;\n}\n.organization-name[data-v-ac171fee] {\n        padding-left: 16px;\n        font-weight: 500;\n        font-size: 20px;\n        line-height: 23px;\n        color: #212121;\n}\n.delete-organization-button[data-v-ac171fee] {\n        background: #CBCBCB;\n        border-radius: 4px;\n        font-size: 14px;\n        line-height: 16px;\n        text-align: center;\n        font-weight: 500;\n        color: #212121;\n        padding: 8px 16px;\n        border: none;\n}\n.no-invitation-waiting[data-v-ac171fee] {\n        font-size: 18px;\n        line-height: 26px;\n        text-align: center;\n        color: #BCBCBC;\n}\n.invite-button[data-v-ac171fee] {\n        background: #2C5A86; \n        border-radius: 4px; \n        border: none; \n        padding: 8px 16px;\n        font-size: 15px; \n        font-weight: 500; \n        color: #FFFFFF;\n        display: flex;\n        flex-direction: row;\n        align-items: center;\n        height: 36px;\n        margin-right: 12px;\n        float: right;\n}\n.button-disabled button[data-v-ac171fee] {\n        cursor: default;\n        pointer-events: none;\n}\n.button-disabled:hover  .premium-account-tooltip[data-v-ac171fee] {\n        visibility: visible;\n        opacity: 1;\n}\n.premium-account-tooltip[data-v-ac171fee] {\n        visibility: hidden;\n        opacity: 0;\n        top: 192px;\n        right: 42px;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/pages/organization/manage.vue"],"names":[],"mappings":";AAkNI;QACI,qBAAqB;AACzB;AACA;QACI,eAAe;QACf,cAAc;QACd,gBAAgB;QAChB,gBAAgB;QAChB,gBAAgB;QAChB,mBAAmB;AACvB;AACA;QACI,mBAAmB;QACnB,yBAAyB;QACzB,8CAA8C;QAC9C,mBAAmB;QACnB,WAAW;AACf;AACA;QACI,iCAAiC;AACrC;AACA;QACI,iCAAiC;QACjC,uBAAuB;AAC3B;AACA;;QAEI,gCAAgC;QAChC,4BAA4B;AAChC;AACA;QACI,gCAAgC;QAChC,4BAA4B;QAC5B,cAAc;QACd,eAAe;QACf,iBAAiB;AACrB;AACA;QACI,mBAAmB;QACnB,cAAc;QACd,gBAAgB;QAChB,eAAe;QACf,iBAAiB;AACrB;AACA;QACI,eAAe;QACf,cAAc;QACd,