import { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\nimport PremiumAccountTooltip from \"../../components/dialogs/premium/premiumAccountTooltip.vue\"; \n\n\n\nconst router = container.resolve<IRouter>(IRouter);\nconst organization = container.resolve<IOrganizationUseCase>(IOrganizationUseCase);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst organizationUseCase = container.resolve<IOrganizationUseCase>(IOrganizationUseCase);\nconst selectedOrganizationName = computed(() => viewModel.user!.selectedOrganization?.name);\n\n\n// const routerParams = router.getRouteParams<{ orgId: string }>();\n\n// const members = ref<Array<IOrganizationMember>>([]);\nconst joinedMembers = ref<Array<IUser_SUSI>>([]);\nconst waitingMembers = ref<Array<IListInvitationsResponseItem>>([]);\nconst dialog = container.resolve<IDialog>(IDialog);\n\nconst goBack = () => {\n    router.back();\n}\n\nconst isOwner = (userId: string) => viewModel.user!.selectedOrganization?.ownerUserIds ? viewModel.user!.selectedOrganization.ownerUserIds.includes(userId) : viewModel.user!.selectedOrganization?.creatorUserId === userId;\n\nfunction calculateDate(createDate: string, format: boolean) {\n    if(!format) {\n        const date = new Date(createDate);\n        if(date) {\n            const minutes = parseInt(String((date.getTime() - (new Date()).getTime()) / (60 * 1000)), 10)\n            const hours = parseInt(String(minutes / 60), 10);\n            const days = parseInt(String(hours / 24), 10);\n            const hoursCalc = hours - (days*24); \n            if(hours > 0) {\n                return days.toFixed() + ` day${days < 2 ? '' : 's'} ` + hoursCalc.toFixed() + ` hour${hoursCalc < 2 ? '' : 's'} `;\n            } \n\n            else {\n                return \"< 1 hour\";\n            }\n        }\n    }\n    else {\n        const formatter = new Intl.DateTimeFormat('en-GB', { year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', minute: 'numeric' });\n        const formattedDate = formatter.format(new Date(createDate));\n        return formattedDate;\n    }\n    \n}\nconst cancelInvitation = async (invitationId: string) => {\n    executor.execute(async () => {\n        await organization.deleteInvitation({ invitationId });\n        await refreshData();\n    }, { loading: true });\n};\nconst removeFromOrganization = async (userId: string) => {\n    if(isOwner(userId)) { return; }\n    executor.execute(async () => {\n        await organization.removeUserFromOrganization({ userId });\n        await refreshData();\n    }, { loading: true });\n};\n\nconst updateOrganizationOwner = async (userId: string, action: \"add\" | \"remove\") => {\n    executor.execute(async () => {\n        await organization.updateOrganizationOwner({ userId, action });\n    }, { loading: true });\n}\n\nfunction openInvitePeopleDialog () {\n    if(viewModel.user?.selectedOrganization?.accountType == \"premium\") {\n        const closeCb = (reason: \"close\" | \"outclick\" | \"component\") => {\n            if(reason == \"component\") {\n                refreshData();\n            }\n        }\n        dialog.showDialog(\n            defineAsyncComponent(() => import(`../../components/dialogs/organization/invitePeopleDialog.vue`)),\n            { closable: true, title: \"Invite People to \" + selectedOrganizationName.value, width: \"658px\", maxHeight: \"700px\", closeCb},\n            \n        );\n    }\n}\n\nfunction deleteOrganization() {\n    const org = viewModel.user!.selectedOrganization!;\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../../components/dialogs/backoffice/deleteConfirmation.vue`)),\n        { closable: true, title: \"Delete Organization\", width: \"577px\", maxHeight: \"206px\" },\n        {\n            infoText: 'Organization Name',\n            placeholder: `Write organization name '${org.name}' to delete`, validText: org.name,\n            deleteCb: async () => {\n                await executor.execute(async () => {\n                    await organizationUseCase.deleteOrganization(org.id, org.name);\n                    close();\n                }, { loading: true });\n            }\n        },\n    );\n}\n\nconst refreshData = async () => {\n    // const response = await organization.getOrganizationMembers({});\n    // members.value = [];\n    // response.forEach(member => members.value.push(member));\n    executor.execute(async () => { \n        await refreshOrganizationInvitationsData();\n        await refreshOrganizationUsersData();\n    }, { loading: true });\n};\nconst refreshOrganizationUsersData = async () => {\n    const response = await organization.listOrganizationUsers();\n    joinedMembers.value = [];\n    response.forEach(member => joinedMembers.value.push(member));\n};\nconst refreshOrganizationInvitationsData = async () => {\n    const response = await organization.listOrganizationInvitations();\n    waitingMembers.value = [];\n    response.forEach(member => waitingMembers.value.push(member));\n};\nexecutor.execute(async () => { await refreshData() }, { loading: true });\n\n\n\n</script>\n<template>\n    <div class=\"organization-header\">\n        <div class=\"back-button-div\" @click=\"goBack\">\n            <i class=\"mdi mdi-arrow-left-box back-button\"></i>\n        </div>\n        <hr style=\"width: 1px; height: 36px; color: #D5D5D5; margin: 0; opacity: 1\">\n        <div class=\"organization-name\">\n            <span>{{ selectedOrganizationName }}</span>\n            <i class=\"mdi mdi-pencil\" style=\"font-size: 20px; color: #757575; margin-left: 14px; display: none;\"></i>\n        </div>\n        <div style=\"margin: 0 0 0 auto;\">\n            <button class=\"delete-organization-button\" @click=\"deleteOrganization\"><i class=\"mdi mdi-delete\" style=\"font-size: 16px; margin-right: 4px;\"></i>DELETE ORGANIZATION</button>\n        </div>\n    </div>\n    <div style=\"margin: 24px 0 16px; display: flex; align-items: center\" >\n        <span class=\"app-text\" style=\"margin: 0 0 0 7px\">Invitations ({{waitingMembers.length}})</span>\n        <div style=\"margin-left: auto\" :class=\"{'button-disabled': viewModel.user?.selectedOrganization?.accountType != 'premium'}\">\n            <button class=\"invite-button\" @click=\"openInvitePeopleDialog\"><i class=\"mdi mdi-account-multiple-plus\" style=\"font-size:20px; margin-right: 4px;\"></i>INVITE PEOPLE</button>\n            <PremiumAccountTooltip class=\"premium-account-tooltip\"></PremiumAccountTooltip>\n        </div>\n    </div>\n    <table v-if=\"waitingMembers.length > 0\" class=\"members-table\">\n            <tr class=\"thead\">\n                <th class=\"col-xl-5\">Mail Address</th>\n                <th class=\"col-xl-3\">Date Invited</th>\n                <th class=\"col-xl-3\">Invitation Expires in</th>\n                <th class=\"col-xl-1\"></th>\n            </tr>\n            <tr v-for=\"member in waitingMembers\" :key=\"member.id\">\n                <td><img src=\"../../../../images/profile-image-dark-in-menu.svg\" style=\"margin-right: 12px;\"/>{{ member.email }}</td>\n                <td>{{ calculateDate(member.createDate, true) }}</td>\n                <td>{{ calculateDate(member.expireDate, false) }}</td>\n                <td>\n                    <i class=\"mdi mdi-dots-vertical member-edit-menu-button\" id=\"dropdownMemberEditMenuButton\" data-bs-toggle=\"dropdown\" aria-expanded=\"false\"></i>\n                    <div style=\"right: 0\" class=\"dropdown-menu member-edit-menu\" aria-labelledby=\"dropdownMemberEditMenuButton\">\n                        <span style=\"display:flex; align-items: center; cursor: pointer;\" @click=\"cancelInvitation(member.id)\"><i class=\"mdi mdi-close remove-icon\"></i>Cancel Invitation</span>\n                    </div>\n                </td>\n            </tr>\n    </table>\n    <div v-else style=\"display: flex; justify-content: center;\">\n        <span class=\"no-invitation-waiting\">There is no invitation waiting.</span>\n    </div>\n    <p class=\"app-text\">Members ({{joinedMembers.length}})</p>\n    <table class=\"members-table\">\n            <tr class=\"thead\">\n                <th class=\"col-xl-5\">Mail Address</th>\n                <th class=\"col-xl-3\">Name</th>\n                <!-- <th class=\"col-xl-3\">Date Joined</th> -->\n                <!-- <th>Status <span><i class=\"mdi mdi-refresh refresh-table\" @click=\"refreshData\"></i></span></th> -->\n                <th class=\"col-xl-1\"></th>\n            </tr>\n            <tr v-for=\"member in joinedMembers\" :key=\"member.id\">\n                <!-- <td v-if=\"member.status == 'joined'\">{{ member.name }}</td>\n                <td v-else></td> -->\n                <td><img src=\"../../../../images/profile-image-dark-in-menu.svg\" style=\"margin-right: 12px;\"/>{{ member.email }}</td>\n                <td><span>{{ member.name + ' ' + member.lastName + ' ' }}</span><span v-if=\"isOwner(member.id)\">(Owner)</span></td>\n                <!-- <td>Date Joined</td> -->\n                <td>\n                    <div class=\"dropdown\">\n                        <i class=\"mdi mdi-dots-vertical member-edit-menu-button dropdown-toggle\" data-bs-toggle=\"dropdown\"></i>\n                        <ul style=\"right: 0\" class=\"dropdown-menu member-edit-menu\" >\n                            <li @click=\"removeFromOrganization(member.id)\" :class=\"{disabled: isOwner(member.id), 'remove-from-organization': true}\" ><i class=\"mdi mdi-account-multiple-minus-outline remove-icon\" ></i>Remove From Organization</li>\n                            <span v-if=\"isOwner(member.id)\" class=\"remove-from-organization-tooltip\">To remove a user from the organization, you must first remove their owner role</span>\n                            <li v-if=\"isOwner(viewModel.user!.userId) && !isOwner(member.id)\" @click=\"updateOrganizationOwner(member.id, 'add')\" ><i class=\"mdi mdi-account-plus-outline remove-icon\"></i>Set as Owner</li>\n                            <li v-if=\"isOwner(viewModel.user!.userId) && isOwner(member.id)\" :class=\"{disabled: isOwner(member.id) && viewModel.user!.selectedOrganization?.ownerUserIds?.length === 1, 'remove-owner': true}\" @click=\"updateOrganizationOwner(member.id, 'remove')\"><i class=\"mdi mdi-account-minus-outline remove-icon\"></i>Remove Owner</li>\n                            <span v-if=\"isOwner(member.id) && viewModel.user!.selectedOrganization?.ownerUserIds?.length === 1\" class=\"remove-owner-tooltip\">There must be at least one owner</span>\n                        </ul>\n                    </div>\n                </td>\n            </tr>\n    </table>\n</template>\n<style scoped>\n    * {\n        font-family: 'Roboto';\n    }\n    .app-text {\n        font-size: 22px;\n        color: #151233;\n        font-weight: 500;\n        margin-left: 7px;\n        margin-top: 32px;\n        margin-bottom: 1rem;\n    }\n    .members-table {\n        background: #FFFFFF;\n        border: 1px solid #E3E3E3;\n        box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n        border-radius: 10px;\n        width: 100%;\n    }\n    td:hover {\n        background-color: none !important;\n    }\n    tbody > tr:hover {\n        background-color: none !important;\n        border: none !important;\n    }\n    .members-table th\n     {\n        border-bottom: 1px solid #E3E3E3;\n        padding: 17px 86px 17px 36px;\n    }\n    .members-table td {\n        border-bottom: 1px solid #E3E3E3;\n        padding: 13px 86px 13px 36px;\n        color: #212121;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .members-table .thead {\n        background: #F5F5F5;\n        color: #616161;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n    }\n 